<template>
   <div class="shop-content-wrap">
		<template v-if="gridListView==true && listData != null">
			<v-row>
				<v-col
					v-for="(category,index) in listData"
					cols="12" 
					sm="6"
					md="4"
					lg="3"
					:key="index"
				>						
					<product-item :data="category" @deleteProduct="itemDeleted"></product-item>
				</v-col>				
			</v-row>
		</template>
		<template v-else>
			<product-items-list-view :data="listData" @deleteProduct="itemDeleted"></product-items-list-view>			
		</template>

   </div>   
</template>

<script>
import ProductItem from "./ProductItem";
import ProductItemsListView from "./ProductItemsListView.vue";
import products from "Api/products";
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["linksformbackend"])
  },
  props: ["colxs", "colsm", "colmd", "collg", "colxl", "gridListView"],
  data() {
    return {
      products: null,
      headersForListView: [
{
          text: "ID",
          sortable: false,
          value: "id"
        },
        {
          text: "Image",
          sortable: false,
          value: "image"
        },
        {
          text: "Name",
          sortable: false,
          value: "name"
        },
        {
          text: "Company",
          sortable: true,
          value: "company"
        },
        {
          text: "Departments",
          sortable: true,
          value: "Departments"
        },
        {
          text: "Category",
          sortable: true,
          value: "category"
        },
        {
          text: "Price",
          sortable: true,
          value: "price"
        },
        {
          text: "Action",
          sortable: false,
          value: "action"
        }
      ],
      listData: []
    };
  },
  async created() {
    try {
      const res = await products.getProducts()
      // console.log(res)
      res.data.data.forEach(el => {
        // console.log(el)
        this.listData.push({
          id: el._id,
          image: this.linksformbackend+el.pictures.pic1,
          name: el.name.en,
          price: el.price
        })
      });
      // console.log(this.listData)
    } catch (err) {
      console.log(err)
    }
  },
  methods: {
    itemDeleted(item) {
      let deletedToDoList = this.listData;
      let index = deletedToDoList.indexOf(item);
      this.listData.splice(index, 1);
    },
    // async getProductsData() {
    //   try {
        
    //   } catch (err) {
    //     console.log(err.message)
    //   }
    // }
  },
  components: {
    ProductItem,
    ProductItemsListView
  }
};
</script>
